import { BrowserModule } from '@angular/platform-browser';
import { NgModule } from '@angular/core';

import { AppRoutingModule } from './app-routing.module';
import {RouterModule,Routes} from '@angular/router'
import { AppComponent } from './app.component';
import { MainComponent } from './comp/main/main.component';
import { SidebarComponent } from './comp/sidebar/sidebar.component';
import { MainbodyComponent } from './comp/mainbody/mainbody.component';
import { SidebarSectionComponent } from './comp/sidebar-util/sidebar-section/sidebar-section.component';
import { HeadermainComponent } from './comp/header/headermain/headermain.component';
import { HomeComponent } from './comp/home/home.component';
import { ElectMainComponent } from './comp/prod/electrical/elect-main/elect-main.component';
import { AcMainComponent } from './comp/prod/ac/ac-main/ac-main.component';
import { PlumbMainComponent } from './comp/prod/plumbing/plumb-main/plumb-main.component';


const appRoutes: Routes = [
  { path: 'elect-main', component: ElectMainComponent },
  { path: 'ac-main',   component: AcMainComponent },
  { path: 'plumb-main',   component: PlumbMainComponent
  },
  { path: '', component:HomeComponent
  },
  { path: '**', component: HomeComponent }
];


@NgModule({
  declarations: [
    AppComponent,
    MainComponent,
    SidebarComponent,
    MainbodyComponent,
    SidebarSectionComponent,
    HeadermainComponent,
    HomeComponent,
    ElectMainComponent,
    AcMainComponent,
    PlumbMainComponent
  ],
  imports: [
    BrowserModule,
    AppRoutingModule,
    RouterModule.forRoot(
      appRoutes,
      { enableTracing: true } // <-- debugging purposes only
    )
  ],
  providers: [],
  bootstrap: [AppComponent]
})
export class AppModule { }
